body {
    background-color: #f9fbfd;
    font-family: 'Lato', sans-serif;
    scroll-behavior: smooth;
}

.App__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.App {
    max-width: 1200px;
    width: 100%;
    border-left: 1px solid #0000001f;
    border-right: 1px solid #0000001f;
    border-bottom: 1px solid #0000001f;
}

.App__content {
    background-color: white;
    padding: 32px;
}

.App__banner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0px;
}

.Gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 24px;
}

.Gallery__image {
    width: calc(50% - 4px);
    margin: 2px;
    border-radius: 8px;
}

.Gallery__image img {
    border-radius: 8px;
}

.App__banner {
    border-radius: 24px;
}

.App_banner img {
    border-radius: 24px;
}

.App__content__title {
    font-size: 28px;
    font-weight: 800;
    margin-bottom: 24px;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
}

.App__content__text {
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 32px;
}

ul {
    list-style: none;
    margin-left: -8px;
}
  
ul li::before {
    content: "\2022";
    color: #ff8e42;
    font-weight: bold;
    display: inline-block;
    width: 1em;
    margin-left: -1em;
}

.Wycieczka__title {
    font-size: 26px;
    font-weight: 600;
    margin-bottom: 16px;
    margin-top: 16px;
    font-family: 'Montserrat', sans-serif;
}

.Wycieczka__text {
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 32px;
    margin-top: 16px;
}

.Wycieczka hr {
    border: 1px solid #00000010;
    margin-bottom: 32px;
}

.App__content__image:hover {
    cursor: pointer;
}

.App__financing__container {
    background-color: rgb(29, 29, 29);
    padding: 16px;
}

.App__financing__text {
    color: white;
    font-weight: 600;
    text-align: center;
}