.Navbar__container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    background-color: white;
    border-bottom: 1px solid #E5E5E5;
}

.TopBar {
    background-color: #ff8e42;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-weight: 600;
    font-size: 12px;
    padding: 4px 0;
    width: 100%;
}

.Navbar {
    max-width: 1200px;
    width: 100%;
    height: 72px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
}

.Navbar__logo {
    position: relative;
    left: 24px;
    top: 2px;
}

.Navbar__hamburger {
    position: relative;
    right: 24px;
    top: 2px;
}

.Navbar__navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    right: 24px;
    top: 2px;
    height: 100%;
}

.Navbar__navigation__element__underline {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 6px;
    background-color: #ff8e42;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}

.Navbar__navigation__element {
    position: relative;
    padding: 0 16px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Navbar__navigation__element a {
    text-decoration: none;
    color: #000000;
    font-weight: 600;
    font-size: 14px;
    padding-bottom: 4px;
}

.Navbar__navigation__element:hover {
    cursor: pointer;
}

.Navbar__hamburger__container {
    background-color: #141414;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 16px 0;
    display: none;
    text-align: right;
}

.Navbar__hamburger__navigation__element {
    padding: 8px 16px;
}

.Navbar__hamburger__navigation__element:hover {
    cursor: pointer;
}

.Navbar__hamburger__navigation__element a {
    text-decoration: none;
    color: #FFFFFF;
    font-weight: 500;
    font-size: 14px;
}

.Navbar__hamburger:hover {
    cursor: pointer;
}